import React from 'react'
import { TeamMember, PersonImage } from './styles'

const Person = ({
  path, name, role, size, info, desc,
}) => {
  const widths = ['80vw', '30vw']
  const imgWidths = ['25vh', '0']
  const paddingSizes = ['1%', '0']
  return (
    <TeamMember style={{ width: widths[size], borderRadius: '2px', padding: '1%' }}>
      <img
        style={{
          height: imgWidths[size], width: 'auto', paddingRight: paddingSizes[size], margin: '0px',
        }}
        src={path}
        alt=""
      />
      <PersonImage>
        <h2 style={{
          fontWeight: 'bold', fontSize: '1.3rem', color: 'black', textAlign: 'left', padding: '0px', margin: '0px',
        }}
        >
          {name}
        </h2>
        <h3 style={{
          fontSize: '1rem', color: 'black', padding: '5px 0px', margin: '0px',
        }}
        >
          {role}
        </h3>
        <p style={{
          fontSize: '1rem', paddingBottom: '15px', margin: '0px', width: '100%',
        }}
        >
          {info}
        </p>
        <p style={{
          fontSize: '1rem', paddingTop: '0px', margin: '0px', width: '100%',
        }}
        >
          {desc}

        </p>
      </PersonImage>
    </TeamMember>
  )
}

export default Person
